* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  /* background: #fff; */
  background: radial-gradient(at top left, rgb(0, 0, 0) 0%, #0a020ac0 100%);
  background-repeat: no-repeat;
  height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
}

.main-container {
  width: 100%;
  height: 100%;
}

/* Nav */

.nav-wrapper {
  width: 100%;
  height: 9vh;
  /* background: rgb(255, 255, 255); */
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.nav-wrapper.active {
  background: rgb(255, 255, 255);
}

.nav-container {
  z-index: 1;
  display: flex;
  flex-direction: row;
  padding: 1.2rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 9vh;
  margin: 0 auto;
  max-width: 1200px;
}

.logo {
  font-family: "Acme", sans-serif;
  font-size: 1.8rem;
  color: #6c1eaf;
  cursor: pointer;
}

.link {
  list-style-type: none;
  float: left;
}

.nav-links .anchor {
  display: inline-block;
  color: white;
  text-decoration: none;
  cursor: pointer;
  font-family: "Varela Round", sans-serif;
  font-size: 1.1rem;
  letter-spacing: 0.7px;
  font-weight: 550;
  line-height: 2rem;
  border-bottom: 3px solid transparent;
  border-radius: 2px;
  transition: all 0.3s ease;
  margin: 0.8rem;
}

.nav-links .anchor:hover {
  border-color: #55198b;
}

.nav-links .scrolled {
  color: black;
}

.nav-links.active .anchor {
  color: black;
}

.anchor.active {
  font-weight: 570;
  border-color: #55198b;
}

.menu-icon {
  display: none;
}

/* Home */
.home-container {
  font-family: "Varela Round", sans-serif;
  display: flex;
  color: white;
  flex-direction: row;
  height: 100%;
  padding: 1rem;
  align-items: center;
  width: 100%;
  min-height: 100vh;
}

.content-container {
  width: 50vw;
  height: 100%;
  flex-direction: column;
  display: flex;
  padding: 1.8rem;
  margin-top: 3rem;
  justify-content: center;
}

.main-content {
  font-size: 4rem;
}

.skill-content {
  margin: 0.6rem 0;
  color: #9c72be;
  font-family: "Varela Round", sans-serif;

  font-weight: 400;
  font-size: 1.5rem;
}

.skill-animation {
  height: 100px;
  width: 100%;
  font-size: 2rem;
  color: #9c72be;
  padding-top: 5%;
  margin: 1rem 0;
}

.hand {
  display: inline-block;
  margin: 0.9rem;
  cursor: pointer;
}

.welcome-content {
  color: gray;
}

.svg-container {
  margin-top: 1.5rem;
  width: 50vw;
  height: 36rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.svg {
  height: 75%;
}

/* Services */

.services-container {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  font-family: "Exo 2", sans-serif;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  padding: 2rem;
}

/* .services-container {
  margin-top: 8rem;
} */

.services-wrapper {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  margin-top: 4rem;
  height: 40vh;
  flex-wrap: wrap;
  flex-direction: row;
}

.services {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  max-width: 500px;
  text-align: center;
  /* background: #e7dddd5c; */
  margin: 1rem;
  /* box-shadow: 0 10px 10px -14px rgba(0,0,0,.2); */
  box-shadow: 3px 2px 10px gray;
  max-height: 70rem;
  border-radius: 20px;
  border: 1px solid transparent;
  transition: all 0.3s ease;
  padding: 3rem;
  text-align: center;
}

.servicesPic {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  max-width: 50%;
  text-align: center;
  margin-bottom: 10%;
  max-height: 50%;
  transition: all 0.3s ease;
}

.servicesSkillsLogos {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  max-width: 50px;
  text-align: center;
  margin-left: 7%;
  margin-top: 3%;
  max-height: 50px;
  transition: all 0.3s ease;
}

.services:hover {
  border-color: gray;
}

.services img {
  height: 2.5rem;
  width: auto;
}

.services img.left {
  margin-left: 0.6rem;
}

.services li {
  list-style-type: none;
  font-family: "Varela Round", sans-serif;
  font-size: 1.1rem;
  font-weight: 500;
  text-align: left;
  margin-bottom: 8px;
}

.services h5 {
  padding-bottom: 2%;
}

.services li p {
  display: inline-block;
  margin-top: 0.3rem;
}

/* Skills */

.skills-container {
  font-family: "Exo 2", sans-serif;
  display: flex;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  min-height: 100vh;
}

.skills-container h1 {
  margin: 2rem 0;
}

/* Portfolio */
.portfolio-container {
  font-family: "Exo 2", sans-serif;
  display: flex;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  margin-top: 20%;
  min-height: 100vh;
}

.portfolio-container h1 {
  margin: 3rem 0;
}

.projects-container {
  min-height: 60vh;
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.projects {
  height: 26rem;
  width: 21rem;
  margin: 1rem;
  /* background: #e7dddd5c; */
  box-shadow: 3px 2px 10px gray;
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.2s ease;
}

.project-images {
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
  border-bottom: 1px solid #55198b;
  height: 70%;
}

#LinkedInManager {
  background-image: url("./images/projects/lnmanager.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#IVR {
  background-image: url("./images/projects/ivr.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#RMS {
  background-image: url("./images/projects/RMS.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#ClothingVibe {
  background-image: url("./images/projects/clothing.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#BurgerVibe {
  background-image: url("./images/projects/burger.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#VisionVibe {
  background-image: url("./images/projects/vision.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#MobVibe {
  background-image: url("./images/projects/mob.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#ChristianMusic {
  background-image: url("./images/projects/ChristianMusic.png");
  background-size: cover;
  background-repeat: no-repeat;
}

#Medicure {
  background-image: url("./images/projects/medicure.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.project-links {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30%;
  justify-content: center;
  align-items: center;
}

.project-links .text {
  width: 100%;
  height: 50%;
  color: black;
  font-family: "Varela Round", sans-serif;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-links .icons {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icons #github {
  background-color: rgb(51, 51, 51);
}

.icons #github:hover {
  background-color: rgb(15, 15, 15);
}

.icons #live {
  background-color: rgb(16, 88, 16);
}

.icons #live:hover {
  background-color: rgb(16, 65, 88);
}

.icons i {
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 1.3rem;
  height: 2.6rem;
  line-height: 2.6rem;
  position: relative;
  text-align: center;
  user-select: none;
  width: 2.6rem;
  border-radius: 2.6rem;
  margin: 0px 5px 10px;
  transition: all 0.2s ease-in 0s;
}

/* Contact */
.contact-container {
  font-family: "Exo 2", sans-serif;
  display: flex;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  min-height: 100vh;
}

.profile-image {
  margin: 1rem;
  box-shadow: 6px 6px 10px gray;
  width: 170px;
  border-color: black;
  border-radius: 50%;
  background-image: url("./images/pic.jpg");
  background-size: cover;
  height: 170px;
}

.cards-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
  min-height: 40vh;
}

.cards {
  align-items: center;
  justify-content: center;
  text-align: center;
  display: flex;
  box-shadow: 3px 2px 10px gray;
  border-radius: 20px;
  flex-direction: column;
  width: 16rem;
  height: 9rem;
  margin: 1rem;
  padding: 1.3rem 0;
}

.contact-icons {
  display: flex;
  align-items: center;
  width: 100%;
  height: 33.33%;
  text-align: center;
  justify-content: center;
}

.contact-text {
  display: flex;
  margin-top: 0.7rem;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
  height: 33.33%;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
}

.contact-links {
  margin-top: 0.7rem;
  display: flex;
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
  width: 90%;
  height: 33.33%;
  align-items: center;
  justify-content: center;
}

.contact-links > span {
  display: block;
}

.contact-links i {
  cursor: pointer;
  margin: 0.3rem;
  display: block;
  font-size: 1.3rem;
  position: relative;
  text-align: center;
  transition: all 0.3s ease;
  padding: 0.1rem;

  user-select: none;
}

.contact-links a {
  text-decoration: none;
}

.contact-links i:hover {
  transform: scale(1.09);
}

.contact-links #github {
  color: rgba(0, 0, 0, 0.884);
}

.contact-links:hover #github {
  color: rgb(0, 0, 0);
}

.contact-links #facebook {
  color: rgba(28, 28, 231, 0.938);
}

.contact-links:hover #facebook {
  color: rgb(28, 28, 231);
}

.contact-links #instagram {
  color: rgba(214, 84, 45, 0.89);
}

.contact-links:hover #instagram {
  color: rgb(214, 84, 45);
}

.contact-links #linkedin {
  color: #3d8eb7ea;
}

.contact-links:hover #linkedin {
  color: #3d8eb7;
}

.contact-links #twitter {
  color: #00b7fff1;
}

.contact-links:hover #twitter {
  color: #00b7ff;
}

.contact-icons i {
  color: #f35166;
  cursor: pointer;
  font-size: 1.7rem;
  position: relative;
  user-select: none;
  transition: all 0.2s ease-in 0s;
}

/* Footer */
.footer-container {
  display: flex;
  color: black;
  font-size: 0.8rem;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  font-family: "Varela Round", sans-serif;
  width: 100%;
  height: 8vh;
}

.footer-container a {
  text-decoration: none;
  font-family: "Varela Round", sans-serif;
  font-weight: 200;
  letter-spacing: 0.5px;
  font-size: 1.3rem;
  color: #6507a8;
  text-shadow: 1px 1px 1px black;
  transition: all 0.3s ease;
}

.footer-container a:hover {
  color: #060c02c5;
  text-shadow: 1px 1px 1px black;
}

/* General */

.button-wrapper {
  width: 100%;
}

.button:hover {
  background-color: #fff;
  color: #55198b;
  transition: all 0.3s ease 0s;
  transform: translateY(-3px);
}

.button-link {
  text-decoration: none;
}

.button {
  background-color: #55198b;
  border: 1px solid #55198b;
  color: #fff;
  font-weight: 700;
  min-width: 200px;
  padding: 13px 22px;
  text-transform: uppercase;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  outline: none;
  margin-top: 3.6rem;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
}

.contact-btn {
  margin-right: 1rem;
}

.heading {
  margin: 2rem 0;
  font-family: "Varela Round", sans-serif;
  color: black;
}

/* Media Queries */
/* 1150 Breakpoint */
@media screen and (max-width: 1150px) {
  .home-container {
    flex-direction: column;
  }
  .content-container {
    padding-top: 5rem;
    width: 100vw;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .svg-container {
    width: 100vw;
  }
}

/*900 Mobile Nav Version */

@media screen and (max-width: 900px) {
  .nav-links {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 350px;
    position: absolute;
    top: 9vh;
    text-align: center;
    align-items: center;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-links.active {
    background: rgba(255, 255, 255, 0.911);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .menu-icon {
    display: block;
    position: relative;
    margin: 0 1rem;
    font-size: 1.8rem;
    cursor: pointer;
  }
}

/* 700 */

@media screen and (max-width: 700px) {
  .svg-container {
    margin-top: 1.5rem;
    width: 50vw;
    height: 36rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .svg {
    height: 60%;
  }
}

/* 500 */
@media screen and (max-width: 500px) {
  button {
    width: 90%;
  }

  .contact-btn {
    margin-right: 0;
  }
}

/* 550 */
@media screen and (max-width: 550px) {
  .content-container {
    font-size: 0.7rem;
  }

  .main-content {
    font-size: 2rem;
  }

  .skill-content {
    margin: 0.6rem 0;
    font-size: 1.2rem;
  }

  .hand {
    font-size: 1.8rem;
  }

  .welcome-content {
    font-size: 0.7rem;
  }
}

/* 500 */
@media screen and (max-width: 500px) {
  .svg {
    height: 40%;
  }
}

/* 415 */

@media screen and (max-width: 415px) {
  .skill-content {
    font-size: 1rem;
  }
}

/* 395*/

@media screen and (max-width: 395px) {
  .heading {
    font-size: 1.2rem;
  }
  .button {
    font-size: 0.8rem;
  }
}
